.sidenav{
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 1;
}
.sidenav_logo{
    width: 120px;
    margin: 25px;
}
.sidenav_buttons{
    display: flex;
    flex-direction: column;
}
.sidenav_button{
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #fff;
    background: 0;
    border: 0;
    margin: 5px 10px;
    padding: 10px 15px;
    border-radius: 15px;
    width: 100%;
}
.sidenav_button:hover{
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.128);
}
.sidenav_button > span{
    font-size: 18px;
    font-weight: bold;
    margin-left: 10px;
}
.icons{
    font-size: 22px;
    font-weight: bold;
}
.sidenav_more{
    position: fixed;
    bottom: 3px;
}

@media (max-width:768px) {
    .container{
        display: none;
    }
}
@media (max-width:998px) {
    .container{
        display: none;
    }
}